body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 20px;
  font-family: "Lato", sans-serif;
  background-color: #eeeeee;
}

.grid {
  display: grid;
  grid-template-columns: minmax(200px, 300px) minmax(280px, 1600px) auto;
  grid-template-rows: auto 250px 1fr;
  height: 100vh;
  width: 100%;
  margin: 0 auto;
  grid-template-areas:
    "logo logo logo"
    "menu gallery puste"
    "nav gallery puste";
}

@media (max-width: 712px) {
  .active h1,
  .done h1 {
    font-size: 20px;
  }
}

@media (max-width: 633px) {
  .empty .style-three {
    padding-top: 64px;
  }
  .gallery {
    padding-top: 40px;
  }
}

@media (max-width: 560px) {
  .grid {
    display: grid;
    grid-template-columns: minmax(120px, 550px);
    grid-template-rows: auto 1fr;
    grid-template-areas:
      "logo"
      "menu"
      "nav"
      "gallery";
  }
  header .logo {
    font-size: 100%;
  }
  .empty {
    display: none;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo {
  z-index: 110px;
}

header .logo {
  width: 25px;
  height: 22px;
  margin-top: 15px;
  margin-left: 15px;
  background-color: lightblue;
  padding: 10px;
  position: absolute;
}

h1 {
  font-size: 30px;
  padding-left: 25px;
  position: relative;
  z-index: 10px;
}

hr.style-three {
  border: 0;
  border-bottom: 1px dashed #ccc;
  z-index: 100;
  padding-top: 13px;
}

.header {
  grid-column: 1/-1;
  grid-row: 1/2;
}

.form_text input {
  width: 50px;
  height: 30px;
  margin: 0 5px 0 5px;
  border: none;
}

hr.new4 {
  border-top: 1px solid #aaaaaa;
  width: 100%;
  margin-top: 0.8rem;
  margin-bottom: 1rem;
}

.centerize {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.centerize button {
  background-color: #aaaaaa;
  color: #eeeeee;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  padding: 10px 15px;
  cursor: pointer;
}

.centerize button:hover {
  background-color: #ff0000;
}

.empty .style-three {
  padding-top: 79px;
}

form select {
  padding: 3px 10px;
  font-size: 15px;
}

input {
  margin: 10px;
  padding: 3px 10px;
  font-size: 15px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 380px));
  grid-gap: 15px;
  gap: 15px;
  padding: 15px 15px;
}

.gallery img {
  padding: 0px 100px;
}

h3,
h4,
p {
  text-align: center;
}

p {
  color: #aaaaaa;
  font-size: 15px;
}

.lighter {
  font-weight: lighter;
}

span a {
  color: #ff0000;
  text-decoration: none;
  border-bottom: dashed 1px #ff0000;
  font-weight: bold;
}

.bolder {
  font-size: 55px;
  font-weight: bolder;
  color: #d8d7d7;
  position: relative;
  display: inline;
  display: flex;
  width: 50px;
  height: 50px;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

hr.new {
  background-color: #ff0000;
  border: 1px solid #ff0000;
  width: 30px;
}

.book button {
  background-color: #aaaaaa;
  color: #eeeeee;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  padding: 10px 15px;
  cursor: pointer;
}

.book button:hover {
  background-color: #008000;
}

.active h1,
.done h1 {
  display: flex;
  justify-content: center;
}

.nav {
  grid-column: 1/2;
  grid-row: 3/4;
  padding-top: 15px;
}

.nav ul {
  list-style-type: none;
}

.nav a {
  display: block;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
}

.nav a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

.nav a.active {
  border-left: 10px #eeeeee solid;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}

.nav li:nth-child(1) a.active {
  border-left-color: #ff0000;
}

.nav li:nth-child(2) a.active {
  border-left-color: #008000;
}

.menu {
  grid-column: 1/2;
  grid-row: 2/3;
  padding-top: 15px;
}

